.pseudo() {
  content: "";
  position: absolute;
}

.list-reset() {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

.bg-image() {
  background-position: 0 0;
  background-repeat: no-repeat;
}

.state-navigation-links() {
  &:not(&--current) {
    &:hover,
    &:focus {
      opacity: 0.6;
    }

    &:active {
      opacity: 0.3;
    }
  }
}

.wrapper-mobile() {
  width: 320px;
  margin: 0 auto;
  padding-left: 20px;
  padding-right: 20px;
}

.wrapper-tablet() {
  width: 768px;
  margin: 0 auto;
  padding-left: 30px;
  padding-right: 30px;
}

.wrapper-small() {
  width: 768px;
  margin: 0 auto;
  padding-right: 70px;
  padding-left: 70px;
}

.wrapper-desktop() {
  width: 1300px;
  margin: 0 auto;
  padding-left: 40px;
  padding-right: 40px;
}
