  font-family: "Oswald", "Arial", sans-serif;
  font-size: 16px;
  text-transform: uppercase;
  color: #ffffff;
  cursor: pointer;
}
.button:hover,
.button:focus {
  background-color: #5eaa2f;
}
.button:active {
  color: rgba(255, 255, 255, 0.3);
}
.hamburger {
  position: relative;
  width: 24px;
  height: 18px;
  background-color: transparent;
  border: none;
  outline: none;
  cursor: pointer;
}
.hamburger--nojs {
  display: none;
}
.hamburger__item {
  content: "";
  position: absolute;
  left: 0;
  width: 24px;
  height: 2px;
  background-color: #222222;
}
.hamburger__item::before,
.hamburger__item::after {
  content: "";
  position: absolute;
  left: 0;
  width: 24px;
  height: 2px;
  background-color: #222222;
  transition-duration: 0.5s;
}
.hamburger__item::before {
  top: -8px;
}
.hamburger__item::after {
  bottom: -8px;
}
.hamburger--close .hamburger__item {
  background-color: transparent;
}
.hamburger--close .hamburger__item::before {
  transform: rotate(-45deg) translate(-6px, 5px);
}
.hamburger--close .hamburger__item::after {
  transform: rotate(45deg) translate(-6px, -6px);
}
